.navModalContainer {
  height: 100svh;
  background-color: white;
  padding: 10px;
  margin: 10px;
}

.modalCloseBtn {
  background-color: transparent;
  border: 0px;
  display: flex;
  align-items: center;
  gap:5px;
}

.modalCloseBtnIcon {
  font-size: 1.5rem;
  padding: 3px 5px;
  background-color: var(--backgroundGray);
  border-radius: 50%;
}

.modalCloseBtnText {
  font-size: 1.25rem;
}

.navModalItem {
  font-size: 1.15rem;
  padding: 0.75rem;
  border-bottom: 2px solid var(--backgroundGray);
  cursor: pointer;
}

.navModalDropdown {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}
